@import url('https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap');
*{
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body{
  background: linear-gradient(to right,#fcf8f3,#f5f2ec);
}

body #login{
  display: flex;
  align-items:flex-start;
  justify-content: center;
  min-height: 100vh;
  padding: 10px;
  font-family: 'Poppins', sans-serif;
  background : linear-gradient(to right,#fcf8f3,#f5f2ec);
  flex-wrap: wrap;
  
}

body #estudiante{
  justify-content: center;
  padding: 10px;
  flex-wrap: wrap;
}

#estudiante{
  background: #611232;
  width: 100%;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
}

#labelOpciones{
  color: #fcfcfc;
  font-style: normal;
}

.container{
  display: flex;
  justify-content: center;
  background: #fff;
  width: 100%;
  /* height: 60%; */
  padding: 100px;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
}

.containerOption{
  display: flex;
  justify-content: center;
}

#seleccion{
  text-align:justify;
  width: 70%;
  /* background-color: beige;  */
  padding-top: 50px;
}

.container .text{
  text-align: center;
  font-size: 41px;
  font-weight: 600;
  font-family: 'Montserrat',sans-serif;
  background: #9d2449;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.container form{
  padding: 30px 0 0 0;
}

.container form .form-row{
  display: flex;
  margin: 32px 0;
}

form{
  min-height: 200px;
}

form .form-row .input-data{
  width: 100%;
  height: 40px;
  margin: 0 20px;
  position: relative;
}

form .form-row .textarea{
  height: 70px;
}

.input-data input,
.textarea textarea{
  display: block;
  width: 100%;
  height: 100%;
  border: none;
  font-size: 17px;
  border-bottom: 2px solid rgba(0,0,0, 0.12);
}

.input-data input:focus ~ label, .textarea textarea:focus ~ label,
.input-data input:valid ~ label, .textarea textarea:valid ~ label{
  transform: translateY(-20px);
  font-size: 14px;
  color: #9d2449;
}

.textarea textarea{
  resize: none;
  padding-top: 10px;
}

.input-data label{
  position: absolute;
  pointer-events: none;
  bottom: 10px;
  font-size: 16px;
  transition: all 0.3s ease;
}

.textarea label{
  width: 100%;
  bottom: 40px;
  background: #fff;
}

.input-data .underline{
  position: absolute;
  bottom: 0;
  height: 2px;
  width: 100%;
}

.input-data .underline:before{
  position: absolute;
  content: "";
  height: 2px;
  width: 100%;
  background: #9d2449;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.3s ease;
}

button{
  margin: 1rem;
}

#timer{
  margin: 3rem;
}

.input-data input:focus ~ .underline:before,
.input-data input:valid ~ .underline:before,
.textarea textarea:focus ~ .underline:before,
.textarea textarea:valid ~ .underline:before{
  transform: scale(1);
}
